*
{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}



.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10.5rem;
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 5rem;
  margin-bottom: 1rem;
  text-align: center;
}

button {
  font-size: 1.5rem;
  margin: 0rem .5rem;
  padding: 1rem 2rem;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  border: .17rem solid #2196f3;
}

.button_area{
  display: flex;
}

button:hover {
  transform: translateY(-3px);
  border-color: #ff9800;
}

.text_area{
  text-align: center;
  margin: 1.5rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
}

input {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin-top: 2rem;
  border-radius: 0.5rem;
  border: 2px solid #2196f3;
  transition: all 0.3s ease;
}

input:focus {
  outline: none;
  border-color: #ff9800;
}

@media screen and (max-width:320px){
  .container{
    font-size: small;
  }
  .button_area{
    display: flex;
    flex-direction: column;
  }
  .text_area{
    font-size: medium;
  }
  .input_area{
    width: 12rem;
  }
  button{
    font-size: medium;
    margin-bottom: 1rem;
  }
}

@media screen  and (max-width:650px){
  .button_area{
    display: flex;
    flex-direction: column;
  }
  button{
    margin-bottom: 1rem;
  }
}